.container-pack {
    padding: 0 1rem;
    /* width: 100%; */
    margin-right: auto;
    margin-left: auto;
}

.row-pack {
    width: 100%;
}
.cardtitle {
    color: white;
}
.cardtitle:hover {
    color: #1bc2ca;
}
.row-pack, .form-row-pack {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-left: -1rem;
    margin-right: -1rem;
}
.card-inner, .items-list {
    display: flex;
    flex-direction: column;
}

.card-head {
    /* padding: 1.5rem 2rem; */
    padding: 1rem 2rem;
}

.display-5-pack {
    font-family: 'Oswald',sans-serif;
    font-size: 1.5rem;
    line-height: 1.2;
}

.section-title-pack, .section-subtitle, .pkgcardtitle, .card-subtitle {
    letter-spacing: -1px;
    font-weight: 500;
}

.display-2-pack {
    font-family: 'Oswald',sans-serif;
    font-size: 2.5rem;
    line-height: 1.2;
}

.items-list-wrap {
    padding: 24px 20px 0;
}

.items-list {
    list-style-type: none;
    padding: 0;
}

.cid-traOecPbxG .list-item {
    border-bottom: 1px solid #cccccc;
}

.cid-traOecPbxG .card-inner, .cid-traOecPbxG .card-head, .cid-traOecPbxG .card-price-wrap, .cid-traOecPbxG .list-item {
    background-color: #292845;
}

.items-list .list-item, .card-price-wrap {
    padding: 1rem 2rem;
}

.align-center .list-item {
    justify-content: center;
}
.cid-traOecPbxG .card-head, .cid-traOecPbxG .card-inner {
    border-radius: 15px;
}

.card {
    border-radius: .625rem;
    position: relative;
    background-color: transparent;
    border: none;
    width: 100%;
    padding: 0 1rem;
}

.price-sign {
    margin-right: 0.2rem;
}

.price-value {
    letter-spacing: 0.05rem;
}

.cid-traOecPbxG .list-item:last-of-type {
    padding-bottom: 1.5rem;
    border-bottom: none;
}

.card-price-wrap {
    border-bottom-left-radius: 0.9375rem;
    border-bottom-right-radius: 0.9375rem;
}
.cid-traOecPbxG .price-wrap1 {
